import React from 'react';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';

import propTypes, { defaultProps } from './propTypes';
import styles from './styles';
import { Grid } from '@material-ui/core';

const ContactSection = ({ classes }) => (
  <div className={classes.root}>
    <Typography variant="h2" color="inherit" className={classes.heading}>
      Contact
    </Typography>
    <Grid container direction="row" justify="center" alignItems="center" spacing={16}>
      <Grid item xs={12} sm={6}>
        <div className={classes.contact}>
          <Typography variant="h6" color="inherit" className={classes.phone}>
            0408 242 015
          </Typography>
          <Typography variant="h6" color="inherit" >
            southerncrossrubbishremoval@gmail.com
          </Typography>
        </div>
        <Typography variant="h6" color="inherit" >
          We service eastern suburbs of Melbourne Victoria including
        </Typography>
        <Typography variant="h6" color="inherit" >
          Kilsyth, Mooroolbark, Chirnside Park, Ringwood, Croydon, Bayswater, Boronia, Wantirna, Lilydale, Montrose, Ferntree Gully
        </Typography>
      </Grid >
      <Grid item xs={12} sm={6}>
        Map to go here
      </Grid>
    </Grid>
  </div>
);

ContactSection.propTypes = propTypes;
ContactSection.defaultProps = defaultProps;

export default withStyles(styles)(ContactSection);
